<template>
  <div class="modal" :class="{ 'is-active': isActive }" v-if="vaccine">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Edit vaccine</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div class="field">
          <div class="control">
            <input
              class="input"
              type="text"
              v-model="vaccine.name"
              placeholder="Vaccine Name"
            />
          </div>
        </div>
        <div class="field" v-for="(alias, key) in vaccine.aliases" :key="key">
          <div class="control">
            <div class="level">
              <input
                class="input"
                type="text"
                v-model="alias.value"
                placeholder="Aliases"
              />
              <span class="icon has-text-danger" @click="removeAlias(vaccine)">
                <i class="fas fa-minus-circle"></i>
              </span>
              <span class="icon has-text-link" @click="addAlias(vaccine)">
                <i class="fa fa-plus-circle"></i>
              </span>
            </div>
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input
              class="input"
              type="number"
              v-model.number="vaccine.price"
              placeholder="Price"
            />
          </div>
        </div>
        <div class="" style="padding-top:1 em;">
          <button
            class="button is-link is-fullwidth is-medium"
            @click="update(vaccine)"
          >
            Save
          </button>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { generateID } from "../helpers/utility";

export default {
  name: "VaccineEditsFormComponent",
  methods: {
    validateForm() {
      return true;
    },
    update(vaccine) {
      this.$socket.emit("update-vaccine", vaccine);
      this.closeModal();
    },
    populateExamModal(examData) {
      this.fields = examData;
    },
    closeModal() {
      this.$store.dispatch("updateActiveVaccine", null);
      this.$store.dispatch("toggleEditVaccineModalOn", false);
    },
    addAlias(vaccine) {
      vaccine.aliases.push({ value: "" });
    },
    removeAlias(vaccine) {
      if (vaccine.aliases.length > 1) {
        vaccine.aliases.pop();
      }
    },
    removeParameter(vaccine) {
      if (this.fields.parameters.length > 1) {
        vaccine.parameters.pop();
      }
    },
    addParameter(vaccine) {
      vaccine.parameters.push({ name: "", unit: "", computed: "" });
    }
  },
  computed: {
    isActive() {
      return this.$store.state.labtech.isEditVaccineModalOn;
    },
    vaccine() {
      return this.$store.state.labtech.activeVaccine;
    }
  }
};
</script>
