<template>
  <div class="column">
    <div class="level">
      <button class="button is-link" @click="createVaccine()">
        <span class="icon">
          <i class="fas fa-plus"></i>
        </span>
        <span>Create vaccine</span>
      </button>
      <vaccinesearch-component></vaccinesearch-component>
    </div>
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>Name</th>
          <th>Aliases</th>
          <th>Price</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(vaccine, key) in vaccines" :key="key">
          <td>{{ vaccine.name }}</td>
          <td>{{ vaccine.aliases.map(alias => alias.value).join(", ") }}</td>
          <td>{{ vaccine.price }}</td>
          <td>
            <a class="button is-link" @click="edit(vaccine)">
              <span class="icon">
                <i class="far fa-edit"></i>
              </span>
              <span>
                Edit
              </span>
            </a>
          </td>
        </tr>
      </tbody>
    </table>
    <vaccinecreateform-component></vaccinecreateform-component>
    <vaccineeditform-component></vaccineeditform-component>
  </div>
</template>

<script>
import VaccineSearchComponent from "@/components/VaccineSearchComponent";
import VaccineCreateFormComponent from "@/components/VaccineCreateFormComponent";
import VaccineEditFormComponent from "@/components/VaccineEditFormComponent";

export default {
  name: "VaccineRegistrationComponent",
  methods: {
    activateVaccineModal() {
      this.$store.dispatch("toggleCreateVaccineModalOn", true);
    },
    edit(vaccine) {
      this.$store.dispatch("updateActiveVaccine", vaccine);
      this.$store.dispatch("toggleEditVaccineModalOn", true);
    },
    createVaccine() {
      this.$store.dispatch("toggleCreateVaccineModalOn", true);
    }
  },
  created() {
    this.$store.dispatch(
      "fetchLabtechVaccines",
      this.$store.state.authentication.inMemoryToken
    );
    let store = this.$store;
    this.$socket.on("create-vaccine", vaccine => {
      store.dispatch("addCreatedVaccine", vaccine);
    });
  },
  computed: {
    vaccines() {
      return this.$store.state.labtech.vaccines;
    }
  },
  components: {
    "vaccinesearch-component": VaccineSearchComponent,
    "vaccinecreateform-component": VaccineCreateFormComponent,
    "vaccineeditform-component": VaccineEditFormComponent
  }
};
</script>
