<template>
  <div class="modal" :class="{ 'is-active': isActive }">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Create vaccine</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div class="field">
          <div class="control">
            <input
              class="input"
              type="text"
              v-model="fields.name"
              placeholder="Vaccine Name"
            />
          </div>
        </div>
        <div class="field" v-for="(alias, key) in fields.aliases" :key="key">
          <div class="control">
            <div class="level">
              <input
                class="input"
                type="text"
                v-model="alias.value"
                placeholder="Aliase"
              />
              <span class="icon has-text-danger" @click="removeAliasField">
                <i class="fas fa-minus-circle"></i>
              </span>
              <span class="icon has-text-link" @click="addAliasField">
                <i class="fa fa-plus-circle"></i>
              </span>
            </div>
          </div>
        </div>
        <div class="field">
          <div class="control">
            <input
              class="input"
              type="number"
              v-model.number="fields.price"
              placeholder="Price"
            />
          </div>
        </div>
        <div class="" style="padding-top:1 em;">
          <button
            class="button is-link is-fullwidth is-medium"
            @click="createVaccine"
          >
            Create
          </button>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { generateID } from "../helpers/utility";

export default {
  name: "VaccineCreateFormComponent",
  data() {
    return {
      fields: {
        name: "",
        aliases: [{ value: "" }],
        price: ""
      }
    };
  },
  methods: {
    createVaccine() {
      if (this.validateForm()) {
        let vaccine = this.fields;
        vaccine.aliases = vaccine.aliases;
        vaccine.price = parseFloat(vaccine.price);
        vaccine.cost = vaccine.price;
        vaccine.discounts = [];
        vaccine.active_discounts = [];
        vaccine.is_administered = false;
        vaccine.created_at = Date.now();
        vaccine.updated_at = Date.now();
        (vaccine.type = "vaccine"), (vaccine.key = generateID());
        this.$socket.emit("create-vaccine", vaccine);
        this.closeModal();
      }
    },
    validateForm() {
      return true;
    },
    updateExam() {
      this.$socket.emit("update-vaccine", vaccine);
      this.closeModal();
    },
    populateExamModal(examData) {
      this.fields = examData;
    },
    closeModal() {
      this.$store.dispatch("toggleCreateVaccineModalOn", false);
    },
    addAliasField() {
      this.fields.aliases.push({ value: "" });
    },
    removeAliasField() {
      if (this.fields.aliases.length > 1) {
        this.fields.aliases.pop();
      }
    },
    removeDiscountField() {
      if (this.fields.discounts.length > 1) {
        this.fields.discounts.pop();
      }
    },
    addDiscountField() {
      this.fields.discounts.push({ name: "", value: "" });
    },
    removeParameterField() {
      if (this.fields.parameters.length > 1) {
        this.fields.parameters.pop();
      }
    },
    addParameterField() {
      this.fields.parameters.push({ name: "", unit: "", computed: "" });
    }
  },
  computed: {
    isActive() {
      return this.$store.state.labtech.isCreateVaccineModalOn;
    }
  }
};
</script>
